const FormDetailReservation = ({ t, reservation_id, onChange, reservation, ...props }) => {

    const [payloads, setPayLoads] = useState({
        reservation_status_id: 7
    })
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const user = useSelector(({ auth }) => {
        return auth.user;
    });
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    });
    const updateReservation = () => {

        const service = getService("reservations");
        return service.patch(reservation_id, payloads);
    }
    const handleSend = () => {
        const service = getService("messages");
        service.create({
            "user_id": user.id,
            "reservation_id": Number(reservation_id),
            "entity_id": entity.id,
            ...payloads
        })
            .then(res => {
                message.success("mensaje enviado");
            })
            .catch(err => message.error(err.message));

    }
    const handleSubmit = () => {
        confirm({
            title: t("global.Title.ConfirReservation"),
            content: t("global.Content.ConfirReservation"),
            onOk() {
                updateReservation()
                    .then(res => {
                        message.success(t("global.Reserve.messajeConfir"));
                        navigate("/reservations");
                    })
                    .catch(err => {
                        message.error(err.message);
                    });
            },
            onCancel() { },
        });
    }
    const handleChange = (field, value, form) => {
        form.validateFields((errors, values) => {
            if (!errors) {
                console.log(field, value)
                /* onChange(field, value); */
                setPayLoads({
                    ...payloads,
                    [field]: value
                })
            }
        });
    }
    const disabledStartDate = (startValue) => {
        return (startValue.valueOf() < new Date())
    }
    return (<WrapperForm>
    
    <div>
        {reservation_id && <SimpleForm
            /* onLoad={(data, form) => setForm(form)} */
            title={<h2>{t("reservationDetails.formTitle")}</h2>}
            onChange={handleChange}
            source="reservations"
            autoSubmit={false}
            footer={<div className="section-container-footer">
                <div className="buttom-send">
                <Button 
                className="my-buttom"
                    type="primary"
                    htmlType="submit"
                    onClick={handleSend}
                >Enviar</Button>
                </div>
                <div className="footer-container">
                    <Button
                    className="styled-buttom"
                        block
                        type="primary"
                        htmlType="submit"
                        onClick={handleSubmit}>
                        {t("Global.confi.Reservations")}
                    </Button>
                </div>
            </div>}
        >
            <InputNumber
                name="total_price_cop"
                placeholder={t("global.price.textField")}
                disabled
                label={t("global.price.textField")}
                initial={reservation.total_price_cop}
            />
            <InputNumber
                placeholder={t("global.price.Discount")}
                disabled
                label={t("global.price.Discount")}
                name="total_price_cop_with_discount"
                initial={reservation.total_price_cop_with_discount}
            />
            <DatePicker
                xtype="date"
                disabledDate={disabledStartDate}
                flex={1}
                disabled={reservation.reservation_status_id != 1}
                name="date"
                label={t("global.date.reservations")}
                placeholder={t("global.date.reservations")}
                initial={reservation.date && moment(reservation.date)}
            />
            <Input.TextArea
                name={[`message_${locale}`]}
                label={t("global.observation.reservation")}
                placeholder={t("global.observation.reservation")}
                flex={1}
            />
        

        </SimpleForm>}
    </div >
    </WrapperForm>);
}

const ReservationsDetails = ({ t, id, ...props }) => {
    const dispatch = useDispatch();
    const [loading, setLoading] = useState(false);
    const [showGroup, setShowGroup] = useState(false);
    const [showForm, setShowForm] = useState(false);
    const [reservation, setReservation] = useState({});
    const [sub_total, setSubTotal] = useState(0);
    const [total, setTotal] = useState(0);
    const [items_packs, setItemsPacks] = useState([]);
    const [pack_reservation, setPackReservation] = useState();
    const [reservation_packs, setReservationPacks] = useState([]);
    const [initialized, setInitialized] = useState(false);
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    });
    const getReservation = async () => {
        if (!loading) {
            const service = getService("reservations");
            setLoading(true);
            service.get(id)
                .then(async reservation => {

                    if (reservation.reservation_status_id != 1) {
                        message.info(t("global.messages.reservations"))
                        return navigate("/reservations");
                    }

                    if (reservation && reservation.reservation_packs.length > 0) {
                        console.log("reservation: ", reservation);
                        setReservation(reservation);
                        console.log("Reservation:", reservation);
                        setReservationPacks(reservation.reservation_packs);
                        let entity = reservation["entity"];
                        dispatch({
                            type: CHANGE_ENTITY,
                            entity
                        });
                        await getTotal(reservation.reservation_packs);
                        setLoading(false);
                        setShowForm(true);
                    } else {
                        message.error(t("error.reserve.Details"));
                        navigate("/reservations");
                    }
                })
                .catch(err => {
                    message.error(err.message);
                });
        }
    }
    const getSubTotal = (data, discounted) => {
        let total = 0;
        if (pack_reservation) {
            let meta = JSON.parse(pack_reservation.pack_meta_data);
            let { items_packs } = meta;
            if (items_packs.length == 0)
                total = parseFloat(pack_reservation.price_cop);
            data.forEach(it => {
                let price = parseFloat(it.price_cop);
                if (it.status === "active") {
                    if (it.discount && discounted) {
                        if (it.discount.type == "percentaje") {
                            price = (price * 1 / parseFloat(it.discount.percentaje));
                        } else {
                            price = parseFloat(it.discount.fixed_value);
                        }
                    }
                    total += parseFloat(price);
                }
            });
            if (total == 0 && pack_reservation) {
                //alert(JSON.stringify(pack_reservation))
                //total = pack_reservation.price_cop;
            }
        }
        return total;
    }
    const handleSelectPack = (id, item, record) => {
        let { items_packs } = item;
        setSubTotal(getSubTotal(items_packs));
        setPackReservation(record);
        setItemsPacks(items_packs);
        setShowGroup(true);
    }
    const getTotal = (data) => {
        let total_price_cop = 0;
        let total_price_cop_with_discount = 0;
        let discount_value = 0;
        data = data || reservation_packs;
        data.forEach(pack => {
            let meta = JSON.parse(pack.pack_meta_data);
            let { items_packs } = meta;
            if (items_packs.length == 0)
                total_price_cop += parseFloat(meta.price_cop);
            console.log("meta:::", meta)

            items_packs
                .filter(item => (item.status == "active"))
                .forEach(item => {
                    if (item.discount) {
                        let { type } = item.discount;
                        if (type === 'percentaje') {
                            discount_value += parseFloat(item.price_cop * 1 / item.discount.percentaje);
                        } else {
                            discount_value += parseFloat(item.fixed_value);
                        }
                    }
                    total_price_cop += parseFloat(item.price_cop);
                })
        });
        total_price_cop_with_discount = total_price_cop - discount_value;
        return updateReservation({
            total_price_cop,
            total_price_cop_with_discount
        });
    }
    const onChangeReservation = (field, value) => {
        updateReservation({
            [field]: value
        });
    }
    const updateReservation = (params) => {
        const service = getService("reservations");
        return service.patch(id, params)
            .then(res => {
                setReservation(res);
                if (initialized) {
                    message.success(t("messaje.reserve.Update"));
                } else {
                    setInitialized(true);
                }
            })
            .catch(err => {
                message.error(err.message);
            });
    }
    const updatePackReservation = (params) => {
        const service = getService("reservations-packs");
        service.patch(pack_reservation.id, params)
            .then(res => {
                setItemsPacks(items_packs);
                getTotal();
                console.log("Result:", res["pack_meta_data"]);
            })
            .catch(err => {
                message.error(err.message);
            });
    }
    const handleOnCheckItem = (checked, item, record) => {
        let meta = JSON.parse(pack_reservation.pack_meta_data);
        meta["items_packs"] = items_packs;
        let total = getSubTotal(items_packs);
        setSubTotal(total);
        let discount_value = getSubTotal(items_packs, true);
        //meta["price_cop"] = total;
        //meta["price_cop_discount"] = discount_value;
        pack_reservation["price_cop"] = total;
        pack_reservation["discount_value"] = discount_value;
        pack_reservation["pack_meta_data"] = JSON.stringify(meta);
        console.log("Pack Reservation:", pack_reservation);
        updatePackReservation(pack_reservation);
    }
    const removeReservation = () => {
        const service = getService("reservations");
        return service.remove(id)
            .then(res => {
                message.success(t("global.reserve.message.delete"));
                navigate("/reservations");
            })
            .catch(err => {
                message.error(err.message);
            })

    }
    const handleRemovePack = id => {
        let msg = t("messaje.alert")
        if (reservation_packs.length == 1) {
            msg = t("messaje.warning");
        }
        confirm({
            title: t("alert.title.Attention"),
            content: msg,
            onOk() {
                const service = getService("reservations-packs");
                service.remove(id)
                    .then(res => {
                        if (reservation_packs.length == 1) {
                            return removeReservation();
                        }
                        message.success(t("messaje.pack.Delete"));
                        getReservation();
                    })
                    .catch(err => {
                        message.error(err.message);
                    });
            },
            onCancel() { },
        });
    }
    useEffect(() => {
        if (id) {
            getReservation();
        }
    }, [id]);
    useEffect(() => {
        if (pack_reservation)
            setSubTotal(getSubTotal(items_packs));
    }, [pack_reservation]);
    //if (loading) return <Loader />;
    return (
        <Wrapper>
            <Row className="section-row"
                gutter={4}
                style={{
                    margin: "40px 10px"
                }} type="flex" justify="center">
                <Col xl={20} lg={24} md={24} xs={24}>
                    <>
                        <div className="name">
                            <h2>{entity && entity.name}</h2>
                        </div>
                        <div className="link">
                            <span>
                                <Link to="/reservations">
                                    <Icon className="icon" type="arrow-left" />
                                </Link>
                            </span>{" "}
                            <div className="title"><span>{t("reservationsDeatils.textField")}</span></div>
                        </div>
                    </>
                </Col>
                <Col xl={20} lg={24} md={24} xs={24}>
                    <Row gutter={16} type="text" justify="space-between" align="top">
                        <Col xl={8} sm={8}>
                            <ListItems
                                refinement={(record) => (record && JSON.parse(record["pack_meta_data"]))}
                                onSelect={handleSelectPack}
                                loading={loading}
                                onRemove={handleRemovePack}
                                actions={{
                                    delete: true/* reservation.reservation_status_id == 1 */
                                }}
                                dataSource={reservation_packs}
                                header={<HeaderList
                                    title={t("reservationsDetails.Title")}
                                    total={pack_reservation && pack_reservation.price_cop}
                                />}
                            />
                        </Col>
                        {showGroup && <Col xl={8} sm={8}>
                            <ListItems
                                actions={{
                                    disable: reservation.reservation_status_id == 1
                                }}
                                header={<HeaderList
                                    title={t("reservationsDetails.Title")}
                                    total={sub_total}
                                    t={t}
                                />}
                                onCheck={handleOnCheckItem}
                                refinement={(record) => (record["item"])}
                                dataSource={items_packs}

                            />
                        </Col>}
                        {!loading && <Col xl={8} sm={8}>
                            <FormDetailReservation
                                onChange={onChangeReservation}
                                reservation_id={id}
                                reservation={reservation}
                                t={t}
                            />
                        </Col>}
                    </Row>
                </Col>
            </Row>
        </Wrapper>
    );
};
export default translate()(ReservationsDetails);