import { List, Switch, Menu, Modal, Col, Row, Avatar, Icon, message, Button, Dropdown, ConfigProvider, Tooltip } from "antd";
import React, { useEffect, useState } from "react";
import { getService } from "../../services/services";
import { DEFAULT_IMAGE, URL_PLACEHOLDER, URL_S3 } from "../../constants/";
import { useSelector, useDispatch } from "react-redux";
import { WrapperItemList } from "./Styles";
import { money } from "../../utils";
import _ from "lodash";
import { SortableContainer, SortableElement, SortableHandle } from 'react-sortable-hoc';
import arrayMove from 'array-move';
import { forEach } from "lodash-es";
import { DinamicForm } from "../com/form/";
import { MyModal } from "../com/MyModal";
import { translate } from "react-polyglot";
const { confirm } = Modal;
const DragHandle = SortableHandle(({ children }) => <span>{children}</span>);
const defaultActions = {
    create: false,
    edit: true,
    delete: false,
    disable: false
}
const ActiveButton = ({t, item, onCheck, record, ...props }) => {
    const [checked, setChecked] = useState(false);
    const handleOnDisabled = (value, item, record) => {
        setChecked(value);
        if (onCheck) onCheck(value, item, record);
    }
    useEffect(() => {
        setChecked(props.checked);
    }, [props.checked]);
    useEffect(() => {
        if (props.defaultChecked)
            setChecked(props.defaultChecked);
    }, [props.defaultChecked]);
    return (<Tooltip title={checked ? t("global.Excluir") : t("global.Add")}>
        <Switch
            checked={checked}
            onChange={(value, event) => {
                event.preventDefault();
                handleOnDisabled(value, item, record);
            }} />
    </Tooltip>
    )
}
const ListItems = ({t, title, fields, source, actionCreateText = "Agregar", reference, resource,
    onLoad, onLoading, onAdd, onRemove, onDisable,
    defaultKeySelected = 0, valueField = "position", showAvatar = true, type = "primary", mode = "modal",
    actions = defaultActions, icon,
    sortable = false,
    onCheck,
    footer,
    header,
    filter, onSelect, refinement,
    refresh,
    initialValues = {},
    ...props }) => {
    const [loading, setLoading] = useState(true);
    const [filterDefaultValues, setFilterDefaultValues] = useState({});
    const [visible, setVisible] = useState(false);
    const [submitting, setSubmitting] = useState(false);
    const [select_ids, setSelectIds] = useState([]);
    const [dataSource, setDataSource] = useState([]);
    const [selectedId, setSelectedId] = useState();
    const [dragId, setDragId] = useState();
    /*  const [locale, setLocale] = useState("es"); */
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const handleClick = (id, record, item) => {
        if (selectedId != id) {
            setSelectedId(id);
            if (onSelect) onSelect(id, record, item);
        }
    }
    const handleOnDisabled = (value, item, record) => {
        if (reference) {
            const service = getService(reference);
            return service.patch(item.id, {
                ["status"]: (value == true ? "active" : "inactive")
            })
                .then(res => {
                    if (onDisable) {
                        onDisable(value, item, record);
                    }
                })
                .catch(err => {
                    message.error(err.message);
                })
        }
        if (onDisable) {
            record["status"] = value ? "active" : "inactive";
            onDisable(value, item, record);
        }
    }
    const onStart = ({ node, index, collection, isKeySorting }, e) => {
        setDragId(node.id);
    }
    const update = (id, params) => {
        if (reference) {
            const service = getService(reference);
            return service.patch(id, {
                ...params
            })
                .then(res => {
                })
                .catch(err => {
                    message.error(err.message);
                })
        }
    }
    const onSortEnd = async ({ oldIndex, newIndex, collection }, e) => {
        try {
            if (oldIndex == newIndex) {
                let item = dataSource.find((item, index) => (index == newIndex));
                if (item) setSelectedId(item.id);
                return;
            };
            let new_array = arrayMove(dataSource, oldIndex, newIndex);
            setDataSource(new_array);
            let count = 0;
            setSubmitting(true);
            /* new_array.forEach(async (item, index) => {
                await update(item.id, {
                    [valueField]: index
                });
                count++;
                if (count == new_array.length) {
                    setSubmitting(false);
                }
            }); */
        } catch (err) {
            message.error(err.message);
        }
    };
    const renderItem = (item, index) => {
        let record = item;
        if (refinement) record = refinement(record);
        let extra_actions = [];
        actions.extra = actions.extra || [];

        if (!reference)
            console.log("Record:", item)
        let checked = typeof item.status != "undefined" ? item.status === "active" : true;
        if (actions.disable) {
            extra_actions.push(
                <ActiveButton
                    t={t}
                    item={item}
                    record={record}
                    defaultChecked={checked}
                    onCheck={check => {
                        item["status"] = check ? "active" : "inactive";
                        if (onCheck) onCheck(check, item, record);
                    }}
                />
            )
        }
        extra_actions = [
            ...extra_actions,
            ...actions.extra,
            actions.edit || actions.delete && <Dropdown overlay={() => (<MenuItems record={item} />)}>
                <Button type="link">
                    <Icon type="more" />
                </Button>
            </Dropdown>
        ]
        return <SortableItem
            key={`item-${record.id}`}
            index={index}
            disabled={!sortable}
            record={record}
            item={item}
            extra_actions={extra_actions} />
    }
    const customizeRenderEmpty = () => (
        <div style={{ textAlign: 'center' }}>
            <Icon type="smile" style={{ fontSize: 20 }} />
            <p>Data Not Found</p>
        </div>
    );
    const getData = () => {
        if (reference) {
            const service = getService(reference);
            console.log("Get Data: ", filterDefaultValues)
            setLoading(true);
            setSelectedId(null);
            if (onLoading) onLoading(true);
            service.find({
                query: {
                    /*  $sort: {
                         [valueField]: 1
                     }, */
                    ...filterDefaultValues
                }
            })
                .then(({ data }) => {
                    data = data.map((it, index) => ({
                        ...it,
                        [valueField]: index
                    }))
                    setDataSource(filter ? data.filter(filter) : data)
                    if (onLoad) onLoad(data);
                    if (onLoading) onLoading(false);
                    setLoading(false);
                })
                .catch(err => {
                    message.error(err.message);
                    setLoading(false);
                });
        }
    }
    const remove = id => {
        if (reference) {
            /* ELIMINAR */
            return confirm({
                title: t("alert.title.Attention"),
                content: t("alert.content.Delete"),
                onOk() {
                    const service = getService(reference);
                    service.remove(id)
                        .then(res => {
                            message.success(t("alert.record.Remove"));
                            if (onRemove) onRemove(res);
                            getData();
                        })
                        .catch(err => {
                            message.error(err.message);
                        });
                },
                onCancel() { },
            });
        }
        if (onRemove) onRemove(id);
    }
    const handleMenuClick = (key, record) => {
        switch (key) {
            case "delete":
                remove(record.id);
                break;
            case "edit":
                setSelectedId(record.id);
                setVisible(true);
                break;
        }
    }
    const handleAdd = () => {
        setSelectedId(null);
        if (mode == "modal")
            setVisible(true);
        if (onAdd) onAdd(source, reference, mode);
    }
    const MenuItems = props => (
        <Menu
            className={props.className || "list-menu"}
            onClick={({ key }) => handleMenuClick(key, props.record)}>
            {actions.edit && <Menu.Item key="edit">
                <Icon type="edit" />
            Editar
          </Menu.Item>}
            {actions.delete && <Menu.Item key="delete">
                <Icon type="delete" />
            {t("global.removed")}
          </Menu.Item>}
        </Menu>
    );
    const SortableItem = SortableElement(({ record, item, extra_actions }) => {
        /* let cover = record.path_main ? `${URL_S3}/${record.path_main}` : DEFAULT_IMAGE; */

        let cover = record.gallery ? record.gallery.gallery_media && record.gallery.gallery_media[0] &&
            record.gallery.gallery_media[0].path ?
            `${URL_S3}/${record.gallery.gallery_media[0].path}` : DEFAULT_IMAGE
            : DEFAULT_IMAGE;
        let isSelected = selectedId ? item.id == selectedId : item.id == defaultKeySelected;

        let price;
        let discount;
        if (record.price_cop) {
            price = parseFloat(record.price_cop)
        }
        /* 
            verificar que el total sea sin el precio del paquete incluido,
            cuando no tiene items, se toma el precio del pack, cuando tiene items, es la sumatoria.
        */
        if (record.discount) {
            console.log("Discount:", record.discount)
            if (price && record.discount) {
                if (record.discount.type == "percentaje") {
                    discount = record.discount.percentaje;
                } else {
                    discount = record.discount.fixed_value;
                }
            }
        }
        return (
            <List.Item
                id={item.id}
                actions={extra_actions}
                className={`item item-${type}  ${isSelected ? "item-selected" : ""}`}
            >
                <List.Item.Meta
                    onClick={() => handleClick(item.id, record, item)}
                    className={`meta-container ${showAvatar ?
                        "meta-container-avatar"
                        : "meta-container-no-avatar"}`}

                    avatar={<div >
                        {type == "primary" && <Icon type={icon || "right"} />}
                        {showAvatar && <Avatar
                            size="large"
                            shape="square"
                            src={cover}
                        />}
                    </div>
                    }
                    title={<div>
                        {/* {record.id} */}
                        {record[`name_${locale}`]}
                    </div>
                    }
                    description={<div className="price-container">
                        <div className="price">{price && money(price)}</div>
                        {
                            record.discount &&
                            <div className="discount">
                                <strong>{t("ListItems.reserveDiscount")}</strong>{" "}
                                {
                                    record.discount.type == "percentaje" ? `${discount}%` :
                                        money(discount)
                                }
                            </div>
                        }
                    </div>
                    }
                />
            </List.Item>
        )
    });
    const SortableList = SortableContainer(({ items }) => {
        return (<WrapperItemList>
            <ConfigProvider renderEmpty={customizeRenderEmpty}>
                <List
                    header={!loading && <div className="list-header">
                        <div className="list-title">
                            {title && <h2>{title}</h2>}
                            {reference && <Button
                                onClick={() => getData()}
                                type="link" loading={submitting || loading} icon="reload" />}
                        </div>
                        <div className="header-conent">
                            {header}
                        </div>
                    </div>}
                    title={title}
                    footer={!loading && (footer || <div className="footer-list">
                        {actions.create && <div className="add-container">
                            <Button
                                onClick={handleAdd}
                                type="link" icon="plus">
                                {actionCreateText}
                            </Button>
                        </div>}
                    </div>)}
                    loading={loading}
                    dataSource={dataSource}
                    renderItem={renderItem}
                >
                </List>
            </ConfigProvider>
            {mode == "modal" && <MyModal
                title={`${selectedId ? "Editar" : "Crear"}`}
                onCancel={() => {
                    setVisible(false);
                }}
                visible={visible}
            >
                <DinamicForm
                    id={selectedId}
                    source={reference}
                    fields={fields}
                    record={initialValues}
                    onSubmit={() => {
                        getData();
                        setSelectedId(null);
                        setVisible(false);
                    }}
                />
            </MyModal>}
        </WrapperItemList>
        );
    });
    useEffect(() => {
        if (props.dataSource) {
            if (props.dataSource.length > 0) {
                let item = props.dataSource[0];
                let record = item;
                record = refinement(record);
                setSelectedId(item.id);
                handleClick(item.id, record, item)
            }
            /*  let newDataSource = props.dataSource.map(it => ({
                 ...it,
                 "status": typeof it.status != "undefined" ? it.status : "active"
             })) */
            setDataSource(filter ? props.dataSource.filter(filter) : props.dataSource)
        }
        if (!reference)
            setLoading(false);
    }, [props.dataSource]);
    useEffect(() => {
        setLoading(props.loading);
    }, [props.loading]);
    useEffect(() => {
        if (!_.isEmpty(filterDefaultValues))
            getData();
    }, [refresh]);
    useEffect(() => {
        if (_.isEmpty(filterDefaultValues)) {
            setFilterDefaultValues(props.filterDefaultValues);
        } else if (!_.isEqual(props.filterDefaultValues, filterDefaultValues)) {
            setFilterDefaultValues(props.filterDefaultValues);
        }
    }, [props.filterDefaultValues]);
    useEffect(() => {
        if (!_.isEmpty(filterDefaultValues))
            getData();
    }, [filterDefaultValues]);
    useEffect(() => {
        setSelectedId(defaultKeySelected);
    }, [defaultKeySelected]);
    return <SortableList
        items={dataSource}
        lockOffset={["0%", "100%"]}
        pressDelay={100}
        lockAxis="y"
        onSortStart={onStart}
        onSortEnd={onSortEnd} />
}
export default translate()(ListItems);
