class S3Button extends Component {
  constructor(props) {
    super(props);
  }
  state = {
    progress: null,
    image: null,
    idComponent: uuid(),
    onChange: null
  };

  componentWillReceiveProps({ record, source, value, ...props }) {
    this.setState({
      image: record ? record[source] : null
    });
  }
  componentDidMount() {
    let { record, source, match, value } = this.props;

    this.setState({
      image: record ? record[source] : null,
      id: this.props.id || uuid()
    });
  }
  onUploadStart = (file, next) => {
    this.setState({ name_file: file.name });
    next(file);
  };

  onSignedUrl = (...props) => { };

  onUploadProgress = (progress, ...props) => {
    this.setState({ progress });
  };

  onUploadError = error => {
    message.error(error.message);
  };

  onUploadFinish = file => {
    let { onChange, onUploaded, source, id } = this.props;
    let image = file.fileKey;
    this.setState({
      image
    });
    if (onChange) onChange(source, image, id);
    if (onUploaded) onUploaded(source, image, id);
  };

  render() {
    let { file = {}, match, label, source } = this.props;
    const { image, id } = this.state;

    if (file.name) return <div className="s3Button mr-2">{file.name}</div>;
    return (
      <Container className={this.props.type == "cover" ? "cover" : "mr-2"}>
        <div className="content">
          {
            <Wrapper
              htmlFor={this.state.idComponent}
              className="wrapper"
              variant="outlined"
              color="primary"
              style={{
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover!important",
                background: `url(${image ? `${URL_S3}/${image}` :
                  this.props.emptyImage ? this.props.emptyImage :
                    this.props.type == "cover" ?
                      "/images/cover.svg" :
                      "/images/user.svg"
                  })`
              }}
            >
              <Icon size="large" type={this.props.icon || "camera"} theme="filled" />
            </Wrapper>
          }
        </div>
        <label className="s3Button">
          <S3Uploader
            id={this.state.idComponent}
            signingUrl="/s3-client/sign"
            signingUrlMethod="GET"
            accept="*/*"
            s3path={`${this.props.path}/${id}/${this.props.finalPath}/`}
            preprocess={this.onUploadStart}
            onSignedUrl={this.onSignedUrl}
            onProgress={this.onUploadProgress}
            onError={this.onUploadError}
            onFinish={this.onUploadFinish}
            signingUrlWithCredentials={true} // in case when need to pass authentication credentials via CORS
            uploadRequestHeaders={{ "x-amz-acl": "public-read" }} // this is the default
            contentDisposition="auto"
            scrubFilename={filename => filename.replace(/[^\w\d_\-.]+/gi, "")}
            server={URL_BASE_API}
            // inputRef={cmp => this.uploadInput = cmp}
            autoUpload={true}
            className="s3-uploader"
            style={{ visibility: "hidden" }}
          />
        </label>
      </Container>
    );
  }
}

export default S3Button;
