import styled from "styled-components";
export const WrapperItemList = styled.div` 
    & .ant-list-item-meta-avatar .anticon{
        font-size: 14px!important;
        color: #c0c0c0!important;
        margin-right: 4px;
    }
    & .ant-list-item-meta{
        align-items: center!important;
    }
    & .ant-list-item-meta-title{
        margin-bottom: 0px!important;
        font-size: 18px!important;
    }
    & .ant-list-items {
        box-shadow: 2px 0px 2px #ccc;
        padding: 0px;
        min-height: 100vh;
    }
    & li.ant-list-item {
        border-left: 6px solid var(--color-white)!important;
        cursor: pointer;
    }
    & .ant-list-item.item.item-selected, li.ant-list-item:hover {
        border-left: 6px solid var(--color-primary)!important;
        background-color:var(--color-pink-light)!important;
    }
    & .item-secundary .ant-list-item-meta-avatar {
        margin-right: 16px;
        margin-left: 8px;
    }
    & .header-conent h2 {
        font-size: 20px;
        margin-bottom: 0px;
    }
`;
export const WrapperGroupList = styled.div`
    
`;